*{
    margin: 0;
    padding: 0;
}
/* .BgPage{
   width: 25vw;
} */
.Container-Bg{
    /* display: flex; */
    /* justify-content: center; */
    background-color: #FFFFFF;
    width: 200px;
    /* flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; */
}

.Cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
.CardBox{
    /* width: 35vw; */
    height: auto;
    /* border: 0.2px solid #FF4D21; */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Sf Medium';
    /* color: #303030; */
    /* margin-left: 5%; */
    /* justify-content: center; */
    padding-bottom: 8%;
    background-color: #FFFFFF;
    margin-bottom: 10%;
}
.CardInfo{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}
.DataAnswer{
    font-family: 'Sf Medium';
    font-size: 1rem;
}

.CardName{
    color: #FF4D21;
    text-transform: capitalize;
    text-shadow: #9B9B9B 0.5px 1.5px 3px;
}

.CardTypes{
    display: flex;
    flex-direction: row;
    justify-content: center;   
    color: #303030;
    text-transform: uppercase;
    font-family: 'Sf Medium';
    /* font-size: 1rem; */
    padding: 2%;
}
.CardType{
    display: flex;
    justify-content: space-evenly;   
    padding: 5px 10px;
    margin: 10px 2px 10px 2px;
    border-radius: 10px;
    background: cyan;
    color: #fff;
}
.CardWeight{
    font-family: 'Sf Medium';
    font-size: 1rem;
    color: #303030;
}
.titles{
    font-family: 'Sf Semibold';
    font-size: 1.2rem;
    color:#FF4D21;
    margin-top: 5%;
}

.CardAbility{
    text-transform: capitalize;
    font-size: 1rem;
}

*{
    margin: 0px;
    padding: 0px;
}

.ContainerBg{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* margin-top: 5%; */
    width: 100%;
}
.MyContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    height: auto;
    align-items: center;
    /* margin-top: 5%; */
    padding: 8% 8% 5% 8%;
    /* background-image: linear-gradient(to bottom, #ff4d21, #ff754f, #ff997b, #ffbaa6, #ffdbd2); */
}
.btn{
    /* z-index: 2; */
    /* width: 15vw; */
    margin: 4.5% 0% 1% 0%;
    color: #FFFFFF;
    display: flex;
    justify-content: center;
}
.btn2{
    /* z-index: 2; */
    width: 100vw;
    /* margin: 4.5% 0% 2% 0%; */
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    /* background-color: #ffdbd2; */
    padding-bottom: 2%;
}
.Btns{
    background-color: #ff4d21;
    border: 2px #303030 solid;
    border-radius: 5px;
    color: #FFFFFF;
    padding: 5px;
    font-family: 'Sf Medium';
    margin: 0% 2% 0% 0%;
    display: flex;
    justify-content: center;
}
.Btns:hover{
    background-color: #ffdbd2;
    color:  #ff4d21;
    cursor: pointer;
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .MyContainer{
        padding: 30% 8% 5% 8%;
    }

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    .MyContainer{
        padding: 12% 8% 5% 8%;
    }
}
